import React, { ReactNode, useEffect, useRef, useState } from "react"
import { Nullable } from "../contexts/types/common"

type TransitionProps = {
  children: (state: TransitionState) => ReactNode
  active: boolean
  durations: {
    mounting: number
    unmounting: number
  }
}

export type TransitionState = "initial" | "mounting" | "mounted" | "unmounting" | "unmounted"

export const Transition: React.FC<TransitionProps> = ({ children, active, durations }) => {

  const [state, setState] = useState<TransitionState>("unmounted")
  const timeout = useRef<Nullable<NodeJS.Timeout>>(null)

  useEffect(() => {
    if (active && state === "unmounted") {
      setState("initial")
      timeout.current = setTimeout(() => setState("mounting"), 10)
    } else if (active && state === "mounting") {
      timeout.current = setTimeout(() => setState("mounted"), durations.mounting)
    } else if (!active && state !== "unmounting" && state !== "unmounted") {
      setState("unmounting")
      timeout.current = setTimeout(() => setState("unmounted"), durations.unmounting)
    }
  }, [active, state])

  useEffect(() => () => (timeout.current && clearTimeout(timeout.current)) as unknown as void, [])

  if (state === "unmounted") return <></>

  return (<>{children(state)}</>)
}
