import React, { useEffect } from "react"
import styled from "@emotion/styled"
import { AuthenticatedHome } from "./AuthenticatedHome"
import { Column, Portrait, Row } from "./_styled/global"
import { useLiveData } from "../contexts/LiveDataContext"

export const IndexPage: React.FC = () => {
  const livedata = useLiveData()

  useEffect(() => {
    const title = document.querySelector("title")
    if (!title) return

    title.innerText = `${location.hostname}: SpoonMinecraft`
  }, [])

  return <Root><AuthenticatedHome />{livedata.state === "disconnected" && <RefreshIt/>}</Root>
}

const Root = styled(Column)`
  width: 100%;
  height: 100%;
  
  background: radial-gradient(farthest-corner at 100% 100%, #231810, #121212);
  
  padding: 50px;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  
  ${Portrait} {
    padding: 20px;
  }
`

const RefreshIt: React.FC = () => {
  return (
    <RefreshItRoot>
      <div>
        Ah-Oh... Connection with server was destroyed unexpectedly.<br/>
        <a href={"/"}>Refresh it</a>&nbsp;to reconnect, or check server is alive!
      </div>
    </RefreshItRoot>
  )
}

const RefreshItRoot = styled(Row)`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000cc;
  
  justify-content: center;
  align-items: center;
  
  & > div {
    color: white;
    font-size: 20px;
    text-align: center;
    line-height: 200%;
    
    ${Portrait} {
      font-size: 12px;
    }
  }
`
