import EventEmitter from "events"

type OmittedEmitterTypes = 'addListener' | 'removeListener' | 'on' | 'off' | 'emit' | 'once'

type TypedEventEmitterType<EventMap extends { [key: string]: any[] }> = Omit<EventEmitter, OmittedEmitterTypes> & {
  on<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, listener: (...args: ListenerArgs) => void): void
  off<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, listener: (...args: ListenerArgs) => void): void
  once<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, listener: (...args: ListenerArgs) => void): void
  emit<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, ...args: ListenerArgs): void
}

export class TypedEventEmitter<EventMap extends { [key: string]: any[] }> {

  typedEmitter: TypedEventEmitterType<EventMap>

  constructor() {
    this.typedEmitter = new EventEmitter() as unknown as TypedEventEmitterType<EventMap>
  }

  on<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, listener: (...args: ListenerArgs) => void): void {
    this.typedEmitter.on(eventType, listener)
  }

  off<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, listener: (...args: ListenerArgs) => void): void {
    this.typedEmitter.off(eventType, listener)
  }

  emit<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, ...args: ListenerArgs): void {
    this.typedEmitter.emit(eventType, ...args)
  }

  once<
    EventType extends keyof EventMap,
    ListenerArgs extends EventMap[EventType]
  >(eventType: EventType, listener: (...args: ListenerArgs) => void): void {
    this.typedEmitter.once(eventType, listener)
  }

  removeAllListeners(): void {
    this.typedEmitter.removeAllListeners()
  }

  setMaxListeners(n: number): void {
    this.typedEmitter.setMaxListeners(n)
  }

  get eventNames(): (string | symbol)[] {
    return this.typedEmitter.eventNames()
  }

  get maxListeners(): number {
    return this.typedEmitter.getMaxListeners()
  }

}