import React, { PropsWithChildren, useCallback, useContext, useEffect, useRef, useState } from "react"
import {
  AuthenticateResponse,
  MinecraftOnlinePlayer,
  MinecraftLog,
  RestApiAuth,
  RestApiClient,
  RestApiContextType, TerrainSurfaceResponse, MinecraftServerProperties
} from "./types/RestApiContext"
import axios, { InternalAxiosRequestConfig } from "axios"
import { Nullable } from "./types/common"
import { useLiveData } from "./LiveDataContext"

type AxiosRequestInterceptor = ((value: InternalAxiosRequestConfig) => (InternalAxiosRequestConfig | Promise<InternalAxiosRequestConfig>)) | null | undefined

const RestApiContext = React.createContext<RestApiContextType>({} as any)

export const RestApiContextProvider: React.FC<PropsWithChildren> = ({ children }) => {

  const livedata = useLiveData()

  const axiosInstance = useRef(axios.create({ baseURL: `${location.protocol}//${location.hostname}:${location.port}` }))
  const authorizationInterceptor = useRef(-1)

  const [me, setMe] = useState<Nullable<string>>(null)
  const [initialized, setInitialized] = useState(false)

  const autoLogin = useCallback(async () => {
    const savedToken = localStorage["spoon-token"]
    if (!savedToken) return setInitialized(true)

    try {
      const me = await axiosInstance.current.get<string>("/me", { headers: { 'Authorization': `Bearer ${savedToken}` } })
      toggleAuthorizationInterceptor(savedToken)
      setMe(me.data)
      livedata.receiver.once("Connected", async data => {
        await axiosInstance.current.put("/auth/livedata", { name: data.identifier })
        livedata.receiver.authorized = true
        livedata.receiver.emit("Authorized")
      })
    } catch (e) {
      // token has been expired, no-op.
    } finally {
      setInitialized(true)
    }
  }, [])

  const toggleAuthorizationInterceptor = (token?: string) => {
    if (!token && authorizationInterceptor.current >= 0) {
      axiosInstance.current.interceptors.request.eject(authorizationInterceptor.current)
      authorizationInterceptor.current = -1
    } else if (token && authorizationInterceptor.current === -1) {
      const interceptor: AxiosRequestInterceptor = config => {
        config.headers["Authorization"] = `Bearer ${token}`
        return config
      }
      authorizationInterceptor.current = axiosInstance.current.interceptors.request.use(interceptor)
    } else {
      console.warn(`unexpected call of "toggleAuthorizationInterceptor"!\n  token: ${token}, interceptor: ${authorizationInterceptor.current}`)
    }
  }

  const client: RestApiClient = {
    hello: () => axiosInstance.current.get<string>("/hello"),
    commands: {
      dispatch: request => axiosInstance.current.post<void>("/api/run", request)
    },
    players: {
      online: () => axiosInstance.current.get<MinecraftOnlinePlayer[]>("/api/players/online"),
      offline: () => axiosInstance.current.get<MinecraftOnlinePlayer[]>("/api/players/offline")
    },
    logs: {
      fetch: () => axiosInstance.current.get<MinecraftLog[]>("/api/logs")
    },
    terrains: {
      surface: request => axiosInstance.current.post<TerrainSurfaceResponse>("/api/terrain", request)
    },
    query: {
      server_properties: () => axiosInstance.current.get<MinecraftServerProperties>("/api/server-properties")
    }
  }

  const auth: RestApiAuth = {
    authenticate: async (username, password) => {
      const { data } = await axiosInstance.current.post<AuthenticateResponse>(`/auth`, { username, password })
      localStorage["spoon-token"] = data.token
      toggleAuthorizationInterceptor(data.token)
      await autoLogin()
    },
    invalidate: () => {
      localStorage.removeItem("spoon-token")
      toggleAuthorizationInterceptor()
      setMe(null)
    },
    me
  }

  useEffect(() => {
    autoLogin().then()
  }, [autoLogin])

  const ContextValue: RestApiContextType = {
    client, auth
  }

  return (
    <RestApiContext.Provider value={ContextValue}>
      {initialized ? children : null}
    </RestApiContext.Provider>
  )
}

export const useRestApi = () => useContext(RestApiContext)
