import React from "react"
import {
  displayDimension,
  displayGameMode,
  MinecraftOfflinePlayer,
  MinecraftOnlinePlayer
} from "../contexts/types/RestApiContext"
import styled from "@emotion/styled"
import { Column, Portrait } from "../pages/_styled/global"

export const MinecraftOnlinePlayerItem: React.FC<{ data: MinecraftOnlinePlayer }> = ({ data }) => {
  return (
    <Root>
      <PlayerItemGraphic
        src={`${location.protocol}//${location.hostname}:${location.port}/api/players/graphic/${data.uniqueId}`}
        alt={`skin graphic of '${data.name}'`}
      />
      <PlayerColumn>
        <PlayerName>{data.name}</PlayerName>
        <PlayerData>
          {data.health.toFixed(2)}<O>hp</O> &nbsp;&nbsp;
          {(data.level + data.exp).toFixed(3)}<O>exp</O> &nbsp;&nbsp;
          {displayGameMode(data.gameMode)} &nbsp;&nbsp;<NewlinePortrait/>
          {data.location.x.toFixed(2)}, {data.location.y.toFixed(2)}, {data.location.z.toFixed(2)}&nbsp;of {displayDimension(data.location.environment)}
        </PlayerData>
      </PlayerColumn>
    </Root>
  )
}

export const MinecraftOfflinePlayerItem: React.FC<{ data: MinecraftOfflinePlayer }> = ({ data }) => {
  return (
    <Root style={{ opacity: 0.5 }}>
      <PlayerItemGraphic
        src={`${location.protocol}//${location.hostname}:${location.port}/api/players/graphic/${data.uniqueId}`}
        alt={`skin graphic of '${data.name}'`}
      />
      <PlayerColumn>
        <PlayerName>{data.name}</PlayerName>
      </PlayerColumn>
    </Root>
  )
}

const Root = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  
  margin-bottom: 8px;
  min-width: 50%;
  
  ${Portrait} {
    align-items: flex-start;
  }
`

const PlayerItemGraphic = styled.img`
  width: 30px;
  height: 30px;
  border: 3px solid white;

  image-rendering: pixelated;
  
  ${Portrait} {
    width: 18px;
    height: 18px;
    border: 2px solid white;
  }
`

const PlayerColumn = styled(Column)`
  margin-left: 12px;
`

const PlayerName = styled.div`
  font-size: 22px;
  ${Portrait} { font-size: 16px; }
`

const PlayerData = styled.div`
  font-size: 12px;
  ${Portrait} { font-size: 10px; }
`

const O = styled.span`
  opacity: 0.6;
`

const NewlinePortrait = styled.div`
  display: none;
  ${Portrait} {
    display: block;
  }
`
