import React from "react"
import { css } from "@emotion/react"
import styled from "@emotion/styled"

type SpacerProps = {
  width?: number
  height?: number
  grow?: boolean
}

export const Spacer = styled.div<SpacerProps>`
    user-select: none;
    
    font-size: 1px;
    
    ${({ width }) => width && css`width: ${width}px; flex-shrink: 0;`}
    ${({ height }) => height && css`height: ${height}px; flex-shrink: 0;`}
    ${({ grow }) => grow && css`flex-grow: 1;`}
`

Spacer.defaultProps = {
  children: <>&nbsp;</>
}

export const Landscape = `@media only screen and (min-width: 1600px)`

export const Portrait = `@media only screen and (max-width: 700px)`

export const Layout = `@media only screen and (max-width: 1600px)`

export const Row = styled.div`
  display: flex;
`

export const Column = styled.div`
  display: flex;
  flex-direction: column;
`

export const Label = styled.label`
  color: #cccccc;
  font-size: 16px;
  
  ${Portrait} {
    font-size: 12px;
  }
`

export const ColoredLabel = styled.label`
  color: #aaaaaa;
  
  :has(+ input:focus) {
    color: #9c5929;
  }
`

export const Input = styled.input`
  background-color: transparent;
  
  outline: none;
  
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom-width: 3px;
  border-bottom-style: solid;
  border-bottom-color: #454545;
  
  font-size: 20px;
  padding: 10px 5px;
  
  transition: border-bottom-color 0.125s linear;
  
  :focus {
    border-bottom-color: #67401e;
  }
  
  ::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
    display: none;
  }
  
  ::placeholder {
    color: #ffffff40;
  }
  
  border-radius: 0;
  
  ${Portrait} {
    font-size: 16px;
  }
`

export const Button = styled.button`
  border: none;
  padding: 10px 25px;
  
  border-radius: 6px;
  background-color: #3e2612;
  box-shadow: 0 0 5px #00000030;
  
  font-size: 18px;
  
  :hover {
    background-color: #4a2d15;
    box-shadow: 0 0 10px #00000030;
  }
  
  ${Portrait} {
    padding: 5px 12px;
    font-size: 14px;
    border-radius: 3px;
  }
`

export const SecondaryButton = styled(Button)`
  background-color: #323232;
  
  :hover {
    background-color: #404040;
  }
`

export const O = styled.span`
  opacity: 0.35;
  line-height: normal;
`
