import { TypedEventEmitter } from "../utils/TypedEventEmitter"
import { Nullable } from "./types/common"
import React, { PropsWithChildren, useContext, useEffect, useState } from "react"
import {
  MinecraftDimension,
  MinecraftGameMode,
  MinecraftOfflinePlayer,
  MinecraftOnlinePlayer, TerrainSurfaceResponse
} from "./types/RestApiContext"

export type PlayerLocationData = {
  type: "PlayerLocation"
  playerId: string
  location: [x: number, y: number, z: number]
  environment: MinecraftDimension
}

export type PlayerViewData = {
  type: "PlayerView"
  playerId: string
  yaw: number
}

export type PlayerConnectData = {
  type: "PlayerConnect"
  player: MinecraftOnlinePlayer
}

export type PlayerDisconnectData = {
  type: "PlayerDisconnect"
  player: MinecraftOfflinePlayer
}

export type DaylightCycleData = {
  type: "DaylightCycle"
  time: number
}

export type PlayerHealthData = {
  type: "PlayerHealth"
  playerId: string
  health: number
}

export type PlayerExpData = {
  type: "PlayerExp"
  playerId: string
  level: number
  exp: number
}

export type PlayerGameModeData = {
  type: "PlayerGameMode"
  playerId: string
  gameMode: MinecraftGameMode
}

export type TerrainData = {
  type: "Terrain"
  terrain: TerrainSurfaceResponse
}

export type CommandResponseData = {
  type: "CommandResponse"
  response: string
}

type LiveData =
  PlayerLocationData | PlayerConnectData | PlayerDisconnectData | DaylightCycleData |
  PlayerHealthData | PlayerExpData | PlayerGameModeData | TerrainData | PlayerViewData |
  CommandResponseData

type WebSocketConnectedData = {
  type: "Connected"
  identifier: string
}

type WebSocketEvent = {
  "Connected": [WebSocketConnectedData]
  "Disconnected": []
  "Authorized": []
}

type LiveDataEvent = {
  "PlayerLocation": [PlayerLocationData]
  "PlayerConnect": [PlayerConnectData]
  "PlayerDisconnect": [PlayerDisconnectData]
  "DaylightCycle": [DaylightCycleData]
  "PlayerHealth": [PlayerHealthData]
  "PlayerExp": [PlayerExpData]
  "PlayerGameMode": [PlayerGameModeData]
  "Terrain": [TerrainData]
  "PlayerView": [PlayerViewData]
  "CommandResponse": [CommandResponseData]
}

const AuthorizationRequiredInitialDataTypes: (keyof LiveDataEvent)[] = []

export type LiveDataSubscriptionRequest = { [key in keyof LiveDataEvent]?: [handler: (...args: LiveDataEvent[key]) => void, extra?: any] | null }

export class LiveDataReceiver extends TypedEventEmitter<LiveDataEvent & WebSocketEvent> {

  socket: WebSocket | null = null

  identifier: Nullable<string> = null

  authorized: boolean = false

  constructor() { super() }

  connect() {
    const newSocket = new WebSocket(`${location.protocol === "http:" ? "ws" : "wss"}://${location.hostname}:${location.port}/livedata`)
    newSocket.addEventListener("message", this.onMessage)
    newSocket.addEventListener("close", this.onClose)

    this.socket = newSocket
  }

  get requireCurrent() {
    if (!this.socket) throw Error("no active socket available!")
    return this.socket
  }

  get state() {
    if (!this.socket) return "closed"
    return ["connecting", "connected", "closing", "closed"][this.socket.readyState] as "connecting" | "connected" | "closing" | "closed"
  }

  private onMessage = (event: MessageEvent<any>) => {
    const data: LiveData | WebSocketConnectedData = JSON.parse(event.data)

    if (data.type === "Connected") {
      this.identifier = data.identifier
      this.emit("Connected", data)
    } else {
      this.emit(data.type, data as any)
    }
  }

  private onClose = () => {
    this.socket = null
    this.emit("Disconnected")
  }

  subscribe<T extends keyof LiveDataEvent>(request: LiveDataSubscriptionRequest) {
    if (this.state !== "connected") return false
    const extra: { [key: string]: string } = { }
    Object.keys(request).forEach(it => {
      const key = it as keyof LiveDataSubscriptionRequest
      const array = request[key]
      if (array && array[1]) extra[it] = JSON.stringify(array[1])
    })
    const message: any = { which: Object.keys(request), operation: "subscribe" }
    if (Object.keys(extra)) message.extra = extra
    this.requireCurrent.send(JSON.stringify(message))
    Object.entries(request).forEach(([key, value]) => { if (value) this.on(key as any, value[0] as any) })
    return true
  }

  unsubscribe<T extends keyof LiveDataEvent>(request: LiveDataSubscriptionRequest) {
    if (this.state !== "connected") return false
    this.requireCurrent.send(JSON.stringify({ which: Object.keys(request), operation: "unsubscribe" }))
    Object.entries(request).forEach(([key, value]) => { if (value) this.off(key as any, value[0] as any) })
    return true
  }

  request(which: keyof LiveDataEvent, extra?: string) {
    const send = () => this.requireCurrent.send(JSON.stringify({ which: [which], operation: "initial_data_request", extra: extra ? { [which]: extra } : null }))

    if (AuthorizationRequiredInitialDataTypes.includes(which) && !this.authorized) {
      this.once("Authorized", send)
      return false
    }

    if (this.state !== "connected") {
      this.once("Connected", send)
      return false
    }

    send()
    return true
  }

}

type LiveDataState = "initial" | "connected" | "disconnected"

const LiveDataContext = React.createContext<{ receiver: LiveDataReceiver, state: LiveDataState }>({ receiver: null as unknown as LiveDataReceiver, state: "initial" })

const Receiver = new LiveDataReceiver()

export const LiveDataContextProvider: React.FC<PropsWithChildren> = ({ children }) => {

  const [state, setState] = useState<LiveDataState>("initial")

  const onConnected = () => setState("connected")
  const onDisconnected = () => setState("disconnected")

  useEffect(() => {
    Receiver.once("Connected", onConnected)
    Receiver.once("Disconnected", onDisconnected)

    return () => {
      Receiver.off("Connected", onConnected)
      Receiver.off("Disconnected", onDisconnected)
    }
  }, [])

  return (
    <LiveDataContext.Provider value={{ receiver: Receiver, state }}>
      {children}
    </LiveDataContext.Provider>
  )
}

export const useLiveData = () => useContext(LiveDataContext)
