import type { Nullable } from "./common"
import type { AxiosResponse } from "axios"

type RestApiResponse<T> = Promise<AxiosResponse<T>>

export type RestApiClient = {
  hello: () => RestApiResponse<string>
  players: RestApiPlayersClient
  commands: RestApiCommandsClient
  terrains: RestApiTerrainsClient
  logs: RestApiLogsClient
  query: RestApiQueryClient
}

export type RestApiPlayersClient = {
  online: () => RestApiResponse<MinecraftOnlinePlayer[]>
  offline: () => RestApiResponse<MinecraftOfflinePlayer[]>
}

export type RestApiCommandsClient = {
  dispatch: (request: CommandDispatchRequest) => RestApiResponse<void>
}

export type RestApiTerrainsClient = {
  surface: (request: TerrainSurfaceRequest) => RestApiResponse<TerrainSurfaceResponse>
}

export type RestApiLogsClient = {
  fetch: () => RestApiResponse<MinecraftLog[]>
}

export type RestApiQueryClient = {
  server_properties: () => RestApiResponse<MinecraftServerProperties>
}

export type MinecraftServerProperties = { [key: string]: string }

export type RestApiAuth = {
  authenticate: (username: string, password: string) => Promise<void>
  invalidate: () => void
  me: string | null
}

export type AuthenticateResponse = {
  token: string
}

export type RestApiContextType = {
  client: RestApiClient
  auth: RestApiAuth
}

export type CommandDispatchRequest = {
  command: string
}

export type TerrainSurfaceRequest = {
  environment: MinecraftDimension
  center: ChunkLocation
  scale: 1 | 3 | 5 | 7 | 9
  limit?: number
}

export type TerrainSurfaceResponse = {
  terrain: TerrainSurface
  palette: string[]
  colors: Nullable<string>[]
}

export type TerrainSurface = {
  shadows: number[]
  blocks: number[]
  limited: number[]
}

export type MinecraftDimension = "overworld" | "the_nether" | "the_end"

export const displayDimension = (dimension: MinecraftDimension) => ["Overworld", "The Nether", "The End"][["overworld", "the_nether", "the_end"].indexOf(dimension)]

export type MinecraftGameMode = "CREATIVE" | "SURVIVAL" | "SPECTATOR" | "ADVENTURE"

export const displayGameMode = (mode: MinecraftGameMode) => ["Creative", "Survival", "Spectator", "Adventure"][["CREATIVE", "SURVIVAL", "SPECTATOR", "ADVENTURE"].indexOf(mode)]

export type MinecraftLog = {
  time: number
  message: string
}

interface MinecraftLocationInterface {
  x: number
  y: number
  z: number
  environment: MinecraftDimension
}

export class MinecraftLocation implements MinecraftLocationInterface{
  x: number; y: number; z: number; environment: MinecraftDimension

  private constructor({ x, y, z, environment }: { x: number, y: number, z: number, environment: MinecraftDimension }) {
    this.x = x
    this.y = y
    this.z = z
    this.environment = environment
  }

  static from(instance: MinecraftLocationInterface) {
    return new MinecraftLocation(instance)
  }

}

interface ChunkLocationInterface {
  x: number
  z: number
}

export class ChunkLocation implements ChunkLocationInterface{
  x: number; z: number

  private constructor({ x, z }: ChunkLocationInterface) {
    this.x = x
    this.z = z
  }

  static from(instance: ChunkLocationInterface) {
    return new ChunkLocation(instance)
  }

}

interface MinecraftOnlinePlayerInterface {
  uniqueId: string
  name: string
  gameMode: MinecraftGameMode
  health: number
  level: number
  exp: number
  location: MinecraftLocation
}

export class MinecraftOnlinePlayer implements MinecraftOnlinePlayerInterface{

  uniqueId: string
  name: string
  gameMode: MinecraftGameMode
  health: number
  level: number
  exp: number
  location: MinecraftLocation

  private constructor(instance: MinecraftOnlinePlayerInterface) {
    this.uniqueId = instance.uniqueId
    this.name = instance.name
    this.gameMode = instance.gameMode
    this.health = instance.health
    this.level = instance.level
    this.exp = instance.exp
    this.location = instance.location
  }

  static from(instance: MinecraftOnlinePlayerInterface) {
    return new MinecraftOnlinePlayer(instance)
  }
}

interface MinecraftOfflinePlayerInterface {
  uniqueId: string
  name: string
}

export class MinecraftOfflinePlayer implements MinecraftOfflinePlayerInterface{

  uniqueId: string
  name: string

  private constructor(instance: MinecraftOfflinePlayerInterface) {
    this.uniqueId = instance.uniqueId
    this.name = instance.name
  }

  static from(instance: MinecraftOfflinePlayerInterface) {
    return new MinecraftOfflinePlayer(instance)
  }

}