import React, { MutableRefObject, PropsWithChildren, useEffect, useLayoutEffect, useRef, useState } from "react"
import styled from "@emotion/styled"
import { useRestApi } from "../contexts/RestApiContext"
import {
  ChunkLocation, MinecraftOnlinePlayer,
  MinecraftDimension,
  TerrainSurfaceRequest, TerrainSurfaceResponse
} from "../contexts/types/RestApiContext"
import {
  DaylightCycleData, LiveDataSubscriptionRequest,
  PlayerLocationData, PlayerViewData,
  useLiveData
} from "../contexts/LiveDataContext"
import { Nullable } from "../contexts/types/common"
import { coerceIn } from "../utils/Number"
import { ReactState } from "../utils/React"
import { keyframes } from "@emotion/react"
import { Input, Layout, O, Portrait, Row, Spacer } from "../pages/_styled/global"

export type MinecraftSurfaceMapScale = 1 | 3 | 5 | 7 | 9

type MinecraftSurfaceMapProps = {
  players: MinecraftOnlinePlayer[]
  center: ReactState<ChunkLocation>
  dimension: ReactState<MinecraftDimension>
  scale: ReactState<MinecraftSurfaceMapScale>
  limit: ReactState<Nullable<number>>
}

export const MinecraftSurfaceMap: React.FC<MinecraftSurfaceMapProps> = props => {

  const {
    center: [center, setCenter],
    dimension: [dimension, setDimension],
    scale: [scale, setScale],
    limit: [limit, setLimit],
    players: players
  } = props

  const rest = useRestApi()
  const livedata = useLiveData()

  const map = useRef<HTMLCanvasElement>(null)

  const xWidthCalculator = useRef<HTMLDivElement>(null)
  const zWidthCalculator = useRef<HTMLDivElement>(null)

  const xCoordinateInput = useRef<HTMLInputElement>(null)
  const zCoordinateInput = useRef<HTMLInputElement>(null)

  const [followTarget, setFollowTarget] = useState<Nullable<string>>(null)
  const [loading, setLoading] = useState(false)

  const [limitCandidate, setLimitCandidate] = useState<string>(limit !== null && limit !== undefined ? `${limit}` : "")
  const [centerCandidateX, setCenterCandidateX] = useState<string>(`${center.x}`)
  const [centerCandidateZ, setCenterCandidateZ] = useState<string>(`${center.z}`)

  const daylight = useRef<HTMLDivElement>(null)
  const daylightCycle = useRef<Nullable<NodeJS.Timeout>>(null)

  const sunset = useRef<HTMLDivElement>(null)

  const currentRequest = useRef<Nullable<TerrainSurfaceRequest>>(null)

  const unpack = (_long: number, bitCount: number) => {
    const longBitsSize = 32
    const result: number[] = []
    const rawBits = _long.toString(2)
    const bits = `${"0".repeat(longBitsSize - rawBits.length)}${rawBits}`
    let shifted = longBitsSize
    while (shifted - bitCount >= 0) {
      result.push(parseInt(bits.substring(shifted - bitCount, shifted), 2))
      shifted -= bitCount
    }
    return result.reverse()
  }

  const requestMap = async (input: Partial<TerrainSurfaceRequest>) => {
    if (!Object.keys(input).includes("limit") && Object.values(input).every(it => it === undefined)) return

    setLoading(true)

    let savedCenter: any
    let savedLimit: any
    if (input.environment) {
      try {
        savedCenter = JSON.parse(localStorage[`map_center_${input.environment}`] ?? null)
        savedLimit = JSON.parse(localStorage[`map_limit_${input.environment}`] ?? null)
      } catch (e) {
        localStorage.removeItem(`map_center_${input.environment}`)
        localStorage.removeItem(`map_limit_${input.environment}`)
      }
    }

    const request: TerrainSurfaceRequest = {
      center: input.center ?? (input.environment ? (savedCenter ?? ChunkLocation.from({ x: 0, z: 0 })) : center),
      environment: input.environment ?? dimension,
      scale: input.scale ?? scale,
    }
    if (limit !== null && limit !== undefined && !input.environment) request.limit = limit
    else if (input.environment) request.limit = savedLimit
    if (Object.keys(input).includes("limit")) request.limit = input.limit

    if (request.limit === undefined || request.limit === null) delete request["limit"]

    const useDefaultLimit = (request.limit === null || request.limit === undefined) && request.environment === "the_nether"
    if (useDefaultLimit) request.limit = 64

    currentRequest.current = request

    const { data: surface } = await rest.client.terrains.surface(request)
    updateMap(surface)
    setCenter(request.center)
    setDimension(request.environment)
    setScale(request.scale)
    setCenterCandidateX(`${request.center.x}`)
    setCenterCandidateZ(`${request.center.z}`)

    if (!useDefaultLimit) {
      setLimit(request.limit ?? null)
      setLimitCandidate(request.limit ? `${request.limit}` : '')
    } else {
      setLimit(prevState => prevState !== undefined && prevState !== null ? null : prevState)
      setLimitCandidate('')
    }

    localStorage[`map_environment`] = request.environment
    localStorage[`map_center_${request.environment}`] = JSON.stringify({ x: request.center.x, z: request.center.z })

    if (!useDefaultLimit)
      localStorage[`map_limit_${request.environment}`] = request.limit ?? null
    else
      localStorage[`map_limit_${request.environment}`] = null

    setLoading(false)
  }

  const updateMap = (surface: TerrainSurfaceResponse) => {
    if (!map.current) return

    const canvas = map.current.getContext("2d")
    if (!canvas) return

    const bitCount = Math.max(surface.palette.length.toString(2).length, 4)

    const blocks = surface.terrain.blocks.map(it => unpack(it, bitCount)).flat()
    const limited = surface.terrain.limited.map(it => unpack(it, 1)).flat()
    const shadows = surface.terrain.shadows.map(it => unpack(it, 2)).flat()

    blocks.forEach((it, index) => {
      const [x, z] = [Math.floor(index / (16 * scale)), index % (16 * scale)].map(it => it * 10)
      const block = surface.colors[it]
      const darken = !!limited[index]
      const shadowed = shadows[index] == 1
      const lighted = shadows[index] == 2

      canvas.fillStyle = block ?? "black"
      canvas.fillRect(x, z, 10, 10)

      if (darken) {
        canvas.fillStyle = "#00000080"
        canvas.fillRect(x, z, 10, 10)
      }

      if (shadowed) {
        canvas.fillStyle = "#00000020"
        canvas.fillRect(x, z, 10, 10)
      }
      if (lighted) {
        canvas.fillStyle = "#ffffff07"
        canvas.fillRect(x, z, 10, 10)
      }

      // const rightTop = canvas.createRadialGradient(scale * 16 * 10 * 2, 0, scale * 16 * 10 / 2, scale * 16 * 10 * 2, 0, scale * 16 * 10 / 10)
      // rightTop.addColorStop(0, "#00000000")
      // rightTop.addColorStop(1, "#00000099")
      //
      // canvas.setTransform(0.5, 0, 0, 1, 0, 0)
      //
      // canvas.fillStyle = rightTop
      // canvas.fillRect((scale * 16 * 10 - scale * 16 * 10 / 2) * 3, 0, scale * 16 * 10 - scale * 16 * 10 / 2, scale * 16 * 10 / 2)
      //
      // canvas.setTransform(1, 0, 0, 1, 0, 0)
    })
  }

  const onCoordinateInput = async () => {
    const candidateX = parseInt(centerCandidateX)
    const candidateZ = parseInt(centerCandidateZ)
    if (isNaN(candidateX) || isNaN(candidateZ)) return

    await requestMap({ center: ChunkLocation.from({ x: candidateX, z: candidateZ }) })
  }

  const onDaylightCycle = (data: DaylightCycleData) => {
    const currentDaylight = daylight.current
    const currentSunset = sunset.current
    if (!currentDaylight || !currentSunset) return

    if (daylightCycle.current) clearInterval(daylightCycle.current)
    
    let time = data.time
    const doDaylight = () => {
      time++

      if ((time >= 23961 || 0 <= time) && time < 12010) {
        currentDaylight.style.backgroundColor = "#00000000"
        currentSunset.style.opacity = "0"
      } else if (12010 <= time && time < 13670) {
        const t = (time - 12010) / (13670 - 12010) * 128 / 255
        const o = 1 - Math.abs(1 - (time - 12010) * (2 / (13670 - 12010)))
        currentDaylight.style.backgroundColor = `rgba(0, 0, 0, ${t})`
        currentSunset.style.transform = "rotateZ(0deg)"
        currentSunset.style.opacity = `${o}`
      } else if (13670 <= time && time < 22331) {
        currentDaylight.style.backgroundColor = "#00000080"
        currentSunset.style.opacity = "0"
      } else if (22331 <= time && time < 23961) {
        const t = (1 - (time - 22331) / (23961 - 22331)) * 128 / 255
        const o = 1 - Math.abs(1 - (time - 22331) * (2 / (23961 - 22331)))
        currentDaylight.style.backgroundColor = `rgba(0, 0, 0, ${t})`
        currentSunset.style.transform = "rotateZ(180deg)"
        currentSunset.style.opacity = `${o}`
      }

      if (time === 24000) time = -1
    }
    
    daylightCycle.current = setInterval(doDaylight, 1 / 20 * 1000)
  }

  const onMapClick = async (event: React.MouseEvent<HTMLCanvasElement>) => {
    if (event.button !== 0) return
    setFollowTarget(null)
  }

  const submitLimit = async () => {
    if (limitCandidate === "") return await requestMap({ limit: undefined })

    const newLimit = parseInt(limitCandidate)
    const nan = isNaN(newLimit)
    const invalidRange =
      (dimension === "overworld" && (newLimit <= -64 || newLimit >= 320)) ||
      ((dimension === "the_nether" || dimension === "the_end") && (newLimit <= 0 || newLimit >= 127))

    if (nan || invalidRange) return setLimitCandidate(limit ? `${limit}` : "")

    await requestMap({ limit: newLimit })
  }

  useEffect(() => {
    const request: LiveDataSubscriptionRequest = {
      "DaylightCycle": [onDaylightCycle],
      "PlayerLocation": null,
      "PlayerView": null
    }

    const async = async () => {
      const subscribe = () => livedata.receiver.subscribe(request)
      const subscribed = subscribe()
      if (!subscribed) livedata.receiver.once("Connected", subscribe)
    }

    async().then()

    return () => livedata.receiver.unsubscribe(request) as unknown as void
  }, [])

  useEffect(() => {
    livedata.receiver.request("DaylightCycle")
  }, [])

  useEffect(() => {
    if (!daylight.current || !sunset.current) return

    if (dimension === "overworld") {
      daylight.current.style.display = "block"
      sunset.current.style.display = "block"
    } else {
      daylight.current.style.display = "none"
      sunset.current.style.display = "none"
    }
  }, [dimension])

  useEffect(() => {
    requestMap({ center, environment: dimension, scale }).then()
  }, [])

  useLayoutEffect(() => {
    if (!xCoordinateInput.current || !xWidthCalculator.current) return
    xCoordinateInput.current.style.width = `${xWidthCalculator.current.clientWidth}px`
  }, [centerCandidateX])

  useLayoutEffect(() => {
    if (!zCoordinateInput.current || !zWidthCalculator.current) return
    zCoordinateInput.current.style.width = `${zWidthCalculator.current.clientWidth}px`
  }, [centerCandidateZ])

  return (
    <Root>
      <Map ref={map} width={scale * 16 * 10} height={scale * 16 * 10} onClick={onMapClick}></Map>
      <Daylight ref={daylight} />
      <Sunset ref={sunset}/>
      {players.map(it =>
        <PlayerMarker
          key={it.uniqueId}
          player={it}
          followTarget={[followTarget, setFollowTarget]}
          center={center}
          dimension={dimension}
          scale={scale}
          requestMap={requestMap}
        />
      )}
      <GoUp onClick={() => requestMap({ center: ChunkLocation.from({ x: center.x, z: center.z - 1 }) })}/>
      <GoLeft onClick={() => requestMap({ center: ChunkLocation.from({ x: center.x - 1, z: center.z }) })}/>
      <GoDown onClick={() => requestMap({ center: ChunkLocation.from({ x: center.x, z: center.z + 1 }) })}/>
      <GoRight onClick={() => requestMap({ center: ChunkLocation.from({ x: center.x + 1, z: center.z }) })}/>
      <BGContainer1><BG1/></BGContainer1>
      <BGContainer2><BG2/></BGContainer2>
      <MapSettings>
        <MapSettingSectionTitle>Dimension</MapSettingSectionTitle>
        <DimensionSelectContainer>
          <DimensionSelectItem current={dimension} target={"overworld"} requestMap={requestMap}>Overworld</DimensionSelectItem>
          <DimensionSelectItem current={dimension} target={"the_nether"} requestMap={requestMap}>The Nether</DimensionSelectItem>
          <DimensionSelectItem current={dimension} target={"the_end"} requestMap={requestMap}>The End</DimensionSelectItem>
          <DimensionSelectedIndicator index={["overworld", "the_nether", "the_end"].indexOf(dimension)}/>
          <DimensionHoveredIndicator/>
        </DimensionSelectContainer>
        <Spacer height={20}/>
        <MapSettingSectionTitle>Y Limiter</MapSettingSectionTitle>
        <YLimitInput
          type={"number"}
          value={limitCandidate}
          onChange={e => setLimitCandidate(e.currentTarget.value)}
          onKeyDown={e => e.key.toLowerCase() === "enter" && submitLimit()}
          placeholder={dimension === "the_nether" ? "default 64" : "none"}
        />
      </MapSettings>
      <RightBottomSettings>
        <ChunkCoordinateSettings>
          <ChunkCoordinateInput
            ref={xCoordinateInput}
            type={"number"}
            value={centerCandidateX}
            onChange={e => e.currentTarget.value.replace("-", "").length <= 6 && setCenterCandidateX(e.currentTarget.value)}
            onKeyDown={e => e.key.toLowerCase() === "enter" && onCoordinateInput()}
          />
          <O>x</O>&nbsp;&nbsp;
          <ChunkCoordinateInput
            ref={zCoordinateInput}
            type={"number"}
            value={centerCandidateZ}
            onChange={e => e.currentTarget.value.replace("-", "").length <= 6 && setCenterCandidateZ(e.currentTarget.value)}
            onKeyDown={e => e.key.toLowerCase() === "enter" && onCoordinateInput()}
          />
          <O>z</O>
          <ChunkCoordinateWidthCalculator ref={xWidthCalculator}>{centerCandidateX}</ChunkCoordinateWidthCalculator>
          <ChunkCoordinateWidthCalculator ref={zWidthCalculator}>{centerCandidateZ}</ChunkCoordinateWidthCalculator>
        </ChunkCoordinateSettings>
        <RefreshButton currentRequest={currentRequest} requestMap={requestMap}/>
      </RightBottomSettings>
      <TerrainLoadingOverlay active={loading}><TerrainLoadingProgressBar><TerrainLoadingProgressIndicator/></TerrainLoadingProgressBar></TerrainLoadingOverlay>
    </Root>
  )
}

const RefreshButton: React.FC<{ currentRequest: MutableRefObject<Nullable<TerrainSurfaceRequest>>, requestMap: (response: TerrainSurfaceRequest) => Promise<void> }> = ({ currentRequest, requestMap }) => {

  const [requestCooldown, setRequestCooldown] = useState(0)
  const interval = useRef<Nullable<NodeJS.Timeout>>(null)

  const onClick = async () => {
    if (!currentRequest.current) return
    await requestMap(currentRequest.current)
    setRequestCooldown(5)
    const stateAction = (prevState: number): number => {
      if (prevState === 0) {
        if (interval.current) clearInterval(interval.current)
        return prevState
      }
      return prevState - 1
    }
    interval.current = setInterval(() => setRequestCooldown(stateAction), 1000)
  }

  useEffect(() => (() => interval.current && clearInterval(interval.current)) as unknown as void, [])

  return (
    <RefreshButtonStyled onClick={onClick} disabled={!currentRequest.current || requestCooldown !== 0}>
      <RefreshIcon src={"/refresh.svg"} alt={""}/>
    </RefreshButtonStyled>
  )
}

const DimensionSelectItem: React.FC<PropsWithChildren<{ current: MinecraftDimension, target: MinecraftDimension, requestMap: (input: Partial<TerrainSurfaceRequest>) => Promise<void> }>> = ({ children, current, target, requestMap }) => {
  return (<DimensionSelectItemButton active={current === target} onClick={current !== target ? (() => requestMap({ environment: target })) : undefined}>{ children }</DimensionSelectItemButton>)
}

type PlayerMarkerProps = {
  player: MinecraftOnlinePlayer
  followTarget: ReactState<Nullable<string>>
  center: ChunkLocation
  dimension: MinecraftDimension
  scale: number
  requestMap: (input: Partial<TerrainSurfaceRequest>) => Promise<void>
}

const PlayerMarker: React.FC<PlayerMarkerProps> = props => {

  const {
    player,
    followTarget: [followTarget, requestFollow],
    center: center,
    dimension: dimension,
    requestMap,
    scale
  } = props

  const livedata = useLiveData()

  const marker = useRef<HTMLDivElement>(null)
  const viewIndicator = useRef<HTMLDivElement>(null)

  const lastLocationData = useRef<Nullable<PlayerLocationData>>(null)

  const onPlayerLocation = async (data: PlayerLocationData) => {
    if (data.playerId !== player.uniqueId) return

    lastLocationData.current = data

    const [blockX, , blockZ] = data.location

    const radius = Math.floor(scale / 2)
    const fromX = (center.x - radius) * 16
    const fromZ = (center.z - radius) * 16

    const normalizedX = blockX - fromX
    const normalizedZ = blockZ - fromZ

    const x = coerceIn(normalizedX / (16 * scale) * 100, 0, 100)
    const z = coerceIn(normalizedZ / (16 * scale) * 100, 0, 100)

    if (followTarget === data.playerId) {
      const chunkX = Math.floor(blockX / 16)
      const chunkZ = Math.floor(blockZ / 16)

      const xInRange = center.x - 1 <= chunkX && chunkX <= center.x + 1
      const zInRange = center.z - 1 <= chunkZ && chunkZ <= center.z + 1

      await requestMap({
        center: !xInRange || !zInRange ? ChunkLocation.from({ x: chunkX, z: chunkZ }) : undefined,
        environment: data.environment != dimension ? data.environment : undefined
      })
    }

    if (!marker.current) return

    const previousZ = parseFloat(marker.current.style.top)
    const previousX = parseFloat(marker.current.style.left)

    const distance = Math.sqrt(Math.pow(x - previousX, 2) + Math.pow(z - previousZ, 2))

    if (distance < 10) {
      marker.current.style.transition = "top 0.1s linear, left 0.1s linear"
    } else {
      marker.current.style.transition = "none"
    }

    marker.current.style.top = `${z}%`
    marker.current.style.left = `${x}%`
    marker.current.style.opacity = "1"

    if (data.environment !== dimension) marker.current.style.display = "none"
    else marker.current.style.display = "block"
  }

  const onPlayerView = async (data: PlayerViewData) => {
    if (!viewIndicator.current) return
    viewIndicator.current.style.transform = `translate(-50%, -50%) rotateZ(${data.yaw + 180}deg)`
  }

  useEffect(() => {
    livedata.receiver.on("PlayerLocation", onPlayerLocation)
    livedata.receiver.on("PlayerView", onPlayerView)

    return () => {
      livedata.receiver.off("PlayerLocation", onPlayerLocation)
      livedata.receiver.off("PlayerView", onPlayerView)
    }
  }, [player.uniqueId, center, dimension, scale, followTarget])

  useEffect(() => {
    if (lastLocationData.current) onPlayerLocation(lastLocationData.current).then()
  }, [dimension, center, followTarget])

  useEffect(() => {
    livedata.receiver.request("PlayerLocation", player.uniqueId)
    livedata.receiver.request("PlayerView", player.uniqueId)
  }, [])

  return (
    <PlayerMarkerRoot ref={marker}>
      <PlayerButton onClick={() => requestFollow(player.uniqueId)}>
        <PlayerGraphic
          src={`${location.protocol}//${location.hostname}:${location.port}/api/players/graphic/${player.uniqueId}`}
          alt={`skin graphic of '${player.name}'`}
        />
      </PlayerButton>
      <PlayerViewIndicator ref={viewIndicator}/>
    </PlayerMarkerRoot>
  )
}

