import React, { PropsWithChildren } from 'react'
import { css, Global } from "@emotion/react"
import { RestApiContextProvider } from "./contexts/RestApiContext"
import { IndexPage } from "./pages/IndexPage"
import { LiveDataContextProvider } from "./contexts/LiveDataContext"

const AppContext: React.FC<PropsWithChildren> = ({ children }) => {
  return (
    <LiveDataContextProvider>
    <RestApiContextProvider>
      {children}
    </RestApiContextProvider>
    </LiveDataContextProvider>
  )
}

const App: React.FC = () => {

  return (
    <AppContext>
      <Global styles={GlobalStyles}/>
      <IndexPage />
    </AppContext>
  )
}

const GlobalStyles = css`
  html, body {
    margin: 0;
    background-color: black;
    width: 100%;
    height: 100%;
  }

  #root {
    width: 100%;
    height: 100%;
  }

  * {
    color: white;
  }
`

export default App
